
























import { Component, Prop, Emit, Vue } from 'vue-property-decorator';
interface CustomItem {
  label: string,
  prop: string,
  hidden?: boolean,
  readonly?: boolean
}
@Component
export default class CustomListItem extends Vue {
  @Prop({default: false}) private visible!: boolean;
  @Prop({default: 'physical'}) private type!: string;
  @Prop({default: () => []}) private all_column_item!: Array<CustomItem>;
  @Prop({default: () => []}) private all_item!: Array<any>;
  private select_item: Array<string> = [];
  private created() {
    if ( this.type === 'ecs' ) {
      this.FnHandleSelectItem(this.$store.state.ecs_custom_item)
    }
    else if ( this.type === 'physical' ) {      
      this.FnHandleSelectItem(this.$store.state.custom_host)
    }
  }
  private FnHandleSelectItem(item) {
    if (item.length>0) {
      this.select_item = [...item];
    } else {
      for(let i = 0; i < this.all_column_item.length; i++ ) {
        if (this.select_item.length < 25) {
          if (!this.all_column_item[i].hidden) {
            this.select_item.push(this.all_column_item[i].label)
          }
        } else {
          break
        }
      }
    }
    this.FnEmit()
  }
  private FnConfirm() {
    if(this.select_item.length<5 || this.select_item.length>25){
      this.$message.warning("525")
      return;
    }
    if ( this.type === 'ecs' ) {
      this.$store.commit('SET_ECS_CUSTOM_ITEM', this.select_item)
    }
    else if ( this.type === 'physical' ) {
      this.$store.commit('SET_CUSTOM_HOST', this.select_item)
    }
    this.FnEmit()
    this.FnClose()
  }
  private FnClose() {
    this.$emit('update:visible', false)
  }
  @Emit('fn-custom')
  public FnEmit() {
    return this.select_item
  }
}
