const status = {
  building: '',
  running: '',
  restarting: '',
  shutting_down: '',
  shutdown: '',
  starting_up: '',
  deleting: '',
  deleted: '',
  destroying: '',
  destroy: '',
  updating: '',
  error: '',
  recovering: '',
  failed: ''
}

// {[]}
const auth = {
  delete_ecs: {auth: ['running', 'shutdown', 'error'], msg: '', label: ''},
  recover_ecs: {auth: ['deleted'], msg: '', label: ' '},
  destroy_ecs: {auth: ['deleted', 'failed'], msg: '', label: ' '},
  open_bill: {auth: ['running', 'shutdown'], msg: '', label: ''}
}
const host_status={//error
  start_up_host:{power:['shutdown'],host:['offline','online','exception'],msg:'',label:''},
  shutdown_host:{power:['running'],host:['offline','online'],vm:1,msg:'',msg2:'' ,label:''},
  restart_host:{power:['running'],host:['offline','online'],vm:1,msg:'',msg2:'' ,label:''},
  online_maintenance:{power:[],host:['online','exception'],msg:'',label:''},
  offline_maintenance:{power:[],host:['offline','online','exception'],vm:1,msg:'',label:''},
  finish:{power:[],host:['maintenance','online_maintenance','offline_maintenance'],msg:'',label:''},
  shelves:{power:[],host:['maintenance'],vm:1,msg:'',label:''},
  disperse:{power:['running'],host:['online'],msg:'',label:''},
  out_of_band:{power:[],host:['offline','online'],msg:'',label:''},
  migrate:{power:['running'],host:['online'],msg:'',label:''},
  kvm_to_eci:{power:[],host:['maintenance'],business:['KVM'],msg:'KVM',label:'KVMECI'},
  eci_to_kvm:{power:[],host:['maintenance'],business:['ECI'],msg:'ECI',label:'ECIKVM'},
  bidding:{power:[],host:[],msg:'KVM',business:['KVM'], label:''}
}
export default {
  getInsOperateAuth(type) {
    return auth[type]
  }
}
export const getEcsStatus=(type)=>{
  return status[type]
}
